import {request} from "./request";

//
export function getSongDetail(ids) {
  return request({
    url: '/song/detail',
    params: {
      ids
    }
  })
}

//
export function  getSongUrl(id){
  return request({
    url:'/song/url',
    params:{
      id
    }
  })
}

//
export function getSongText(id){
  return request({
    url:'/lyric',
    params:{
      id
    }
  })
}

//
export function getIsSong(id) {
  return request({
    url: '/check/music',
    params: {
      id
    }
  })
}

//
export function getSongComments(id,limit,offset) {
  return request({
    url: '/comment/music',
    params: {
      id,
      limit,
      offset
    }
  })
}

//
/*
*  :
id :  id,  id,mv id
cid :  id
t :  ,1  ,0 
type:  ,  ,  , mv,  ,  , , 
0:    1: mv   2:    3:    4:    5:    6: 
* */
export function isGood(id,cid,t,type) {
  return request({
    url: 'comment/like',
    params: {
      id,
      cid,
      t,
      type
    }
  })
}

//mv
export function getMvDetail(mvid){
  return request({
    url: '/mv/detail',
    params: {
      mvid
    }
  })
}

//mv
export function getMv(id){
return request({
    url: '/mv/url',
    params: {
      id
    }
  })
}

//MV
export function getMvComments(id,limit,offset) {
  return request({
    url: '/comment/mv',
    params: {
      id,
      limit,
      offset
    }
  })
}


//
export class Songs {
  constructor(songs, privileges) {
    this.name = songs.name  //
    this.id = songs.id      //id
    this.author = songs.ar  //
    this.album = songs.al   //
    this.img = songs.al.picUrl //
  }
}